}

.aboutTitle {
  width: 650px;
  margin-top: 5em;
  line-height: 1em;
  font-size: 3em;
  justify-self: center;
  margin: 0 auto;
  padding-top: 1em;
  padding-bottom: 0.3em;
}

.askInput {
  background: rgb(0, 137, 172);
  border: 0px solid rgb(0, 137, 172);
  border-bottom: 2px solid white;
  border-radius: 0px;
  font-size: 3em;
  width: 40vw;
  text-align: center;
  text-transform: capitalize;
  color: black;
  font-family: "baskervillenormal", serif;
  font-weight: 800;
  letter-spacing: 0.03em;
  text-transform: capitalize;
  -webkit-transform: rotate(-1.75deg);
}

.askInput:focus {
  border: 0;
  border-bottom: 2px solid white;
  outline-style: none;
  box-shadow: none;
}

.askForm {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: auto;
  margin-top: 20vh;
  flex-direction: column;
}

.balanceAbout {
  font-size: 1.8em;
  font-weight: normal;
  margin-top: 2em;
}

.balanceAboutParagraph {
  width: 650px;
  color: rgb(0, 137, 172);
  font-size: 1.6em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.balanceAdress {
  color: rgb(0, 137, 172);
  font-size: 0.4em;
  font-family: "baskervillenormal", serif;
  margin-top: -2em;
}

.balanceAdressBottom {
  color: rgb(0, 137, 172);
  font-size: 1.3em;
  font-weight: bold;
  font-family: "baskervillenormal", serif;
  line-height: 2em;
  margin-bottom: 3em;
  letter-spacing: 0.1em;
}

.balanceAdressBottom:hover {
  color: black;
}

.balanceAdress:hover {
  color: black;
}

.balanceAskBottom {
  margin-bottom: 1em;
}

.balanceButtonB {
  font-family: "baskervillenormal", serif;
